import React, { FC, useState } from 'react'
import clsx from 'clsx'
import DatePicker from 'react-datepicker'
import ru from 'date-fns/locale/ru'
import { CalendarIcon } from '../../icons/Calendar'
import { CalendarHeader } from './parts'
import { CalendarProps } from './types'
import { Typography } from '../../other/Typography'
import { ArrowDownNewIcon } from '../../icons/ArrowDownNew'
import { CloseIcon } from '../../icons/Close'
import { Condition } from '../../other/Condition'
import './Calendar.scss'

const Calendar: FC<CalendarProps> = ({
  className,
  customInputRef,
  datePlaceholder,
  id,
  isMobilePage,
  locale = ru,
  maxDate,
  minDate,
  onCalendarClose,
  onCalendarOpen,
  onChange,
  onClickOutside,
  placeholder,
  popperPlacement,
  tabIndex,
  includeDates,
  excludeDates,
  value,
}) => {
  const [isCalendarOpen, setIsCalendarOpen] = useState(false)

  const onClose = (): void => {
    setIsCalendarOpen(false)
    if (onCalendarClose) {
      onCalendarClose()
    }
  }

  const onOpen = (): void => {
    setIsCalendarOpen(true)
    if (onCalendarOpen) {
      onCalendarOpen()
    }
  }

  return (
    <div
      className={clsx('CalendarNext', className, {
        '--opened': isCalendarOpen,
      })}
    >
      <DatePicker
        className={clsx('CalendarNext__DatePicker', {
          '--has-value': value,
          '--opened': isCalendarOpen,
        })}
        customInputRef={customInputRef}
        dateFormat='dd.MM.yyyy'
        disabledKeyboardNavigation
        excludeDates={excludeDates}
        id={id}
        includeDates={includeDates}
        locale={locale}
        maxDate={maxDate}
        minDate={minDate}
        onCalendarClose={(): void => onClose()}
        onCalendarOpen={(): void => onOpen()}
        onChange={onChange}
        onClickOutside={onClickOutside}
        popperPlacement={popperPlacement}
        renderCustomHeader={CalendarHeader}
        selected={value}
        showPopperArrow={false}
        tabIndex={tabIndex}
      />
      {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
      <label
        className={clsx('CalendarNext__Label', {
          '--has-value': value,
          '--opened': isCalendarOpen,
        })}
        htmlFor={id}
      >
        {placeholder}
      </label>

      <label className='CalendarNext__Icon' htmlFor={id}>
        <Condition match={!isMobilePage || !isCalendarOpen}>
          <CalendarIcon />
        </Condition>
      </label>

      <label
        className={clsx('CalendarNext__Date', {
          '--has-value': value,
          '--opened': isCalendarOpen,
        })}
        htmlFor={id}
      >
        <Typography text={value ? '' : datePlaceholder} type='RegularL' />
      </label>

      <label
        className={clsx(
          {
            CalendarNext__Cross: isMobilePage && isCalendarOpen,
            'CalendarNext__Expand-arrow': !isMobilePage || !isCalendarOpen,
          },
          { '--opened': isCalendarOpen },
        )}
        htmlFor={id}
      >
        {isMobilePage && isCalendarOpen ? (
          <CloseIcon
            color='#8E979F'
            height={24}
            onClick={(): void => onClose()}
            width={24}
          />
        ) : (
          <ArrowDownNewIcon stroke='#606971' strokeWidth='2' width={16} />
        )}
      </label>
    </div>
  )
}

Calendar.defaultProps = {
  datePlaceholder: 'дд / мм / гг',
}

export default Calendar
